@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{
  box-sizing: border-box;
}

body{
  background-color: #111;
  color: #FFF;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
.lists{
  margin-top: -150px;
}

footer{
  margin: 50px 50px;
  text-align: center;
}
.movieRow{
    margin-bottom: 30px;
}
#title {
    margin: 0px 0px 0px 30px;
}
.movieRow--listarea{
    overflow-x: hidden;
    padding-left: 30px;
}
.movieRow--list{
    width: 99999px;
    transition: all ease 0.5s;
}
.movieRow--item{
    display: inline-block;
    width: 150px;
}
.movieRow--item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.2s;
}

.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.movieRow--left, .movieRow--right{
    position: absolute;
    width: 40px;
    height: 225px;
    z-index: 99;
    display: flex;
    align-items: center;
    overflow: hidden;
    background-color: rgba(0,0,0,0.6);
    opacity: 0;
    overflow: hidden;
    cursor: pointer;
    transition: all ease 0.5s;
}
.movieRow:hover, .movieRow--left:hover,
.movieRow:hover, .movieRow--right:hover{
    opacity: 1;
}
.movieRow--left{
    left: 0px;
}
.movieRow--right{
    right: 0px;
}


@media(max-width:760px){
    .movieRow--left,
    .movieRow--right{
        opacity: 1;
    }
}
.featured{
    height: 100vh;
}

.featured--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured--name{
    font-size: 60px;
    font-weight: bold;
}

.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--points, .featured--year, .featured--seasons{
    display: inline-block;
    margin-right: 15px;
}

.featured--points{
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured--buttons{
    margin-top: 15px;
}

.featured--watchbutton, .featured--mylistbutton{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 15px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}
.featured--watchbutton:hover, .featured--mylistbutton:hover{
    opacity: 0.7;
}

.featured--watchbutton{
    background-color: #FFF;
    color: #000;
}   
.featured--mylistbutton{
    background-color: #333;
    color: #FFF;
}
.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #9999;
}
@media(max-width:760px){
    .featured{
        height: 90vh;
    }
    .featured--name{
        font-size: 40px;
    }
    .featured--info{
        font-size: 16px;
    }
    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--watchbutton, .featured--mylistbutton{
        font-size: 16px;
    }
    .featured--genres{
        font-size: 14px
    }
}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0px 30px;
    background: transparent;

    transition: all ease 0.5s;
}
header.black{
    background-color: #111;
}

.header--logo{
    height: 25px;
}
.header--logo img {
    height: 100%;
}
.header--user{
    height: 35px;
}
.header--user img {
    height: 100%;
    border-radius: 2px;
}
